.specification {
	color: #fafcfe;
	padding: 46px 0 63px;
	background-color: #f3951b;
	background-position: center;
	background-size: cover;
	&__inner {
		margin: 0 auto;
		padding: 0 15px;
		max-width: 1625px; }
	&__wrap {
		@include clr; }
	&__left {
		float: left;
		width: 50%;
		@include clr; }
	&__img-wrap {
		float: right;
		padding-top: 10px;
		width: 50%;
		@include clr; }
	&__img {
		position: relative;
		padding: 44px 29px 30px 0;
		max-width: 675px;
		float: right;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			right: 0;
			bottom: 0;
			left: 113px;
			background-color: $white; }
		img {
			position: relative;
			z-index: 1;
			max-width: 100%;
			display: block; } }
	&__content {
		padding-right: 30px;
		width: 100%;
		max-width: 625px;
		float: right; }
	&__title-sm {
		margin-bottom: 55px; }
	&__description {
		font-size: 24px;
		max-width: 500px; }
	&__buttons {
		&:not(:first-child) {
			margin-top: 34px; } }
	&__footer {
		position: relative;
		z-index: 1;
		top: -90px;
		height: 0; } }
