.calc {
    background-position: 100% 102%;
    background-repeat: no-repeat;
    padding-bottom: 103px;
    &__container {
        max-width: 1100px;
        padding: 0 25px;
        margin: 0 auto; }
    &__hero {
        height: 487px;
        text-align: center;
        padding-top: 250px; }
    &__title {
        margin-bottom: 12px;
        font-family: $budidaya;
        font-size: 74px;
        font-weight: normal;
        color: #fbfdff;
        line-height: 1; }
    &__subtitle {
        font-family: $montserrat;
        font-size: 30px;
        font-weight: bold;
        line-height: 1.2;
        color: #f9fbfc; }
    &__descr {
        text-align: center;
        max-width: 870px;
        margin: 50px auto;
        font-family: $montserrat;
        font-size: 18px;
        line-height: 1.67;
        color: #040404;
        font-weight: 600;
        p {
            letter-spacing: -0.03em;
            word-spacing: 0.1em;
            margin-bottom: 32px; } }
    &__descr-title {
        margin-bottom: 24px;
        font-family: $montserrat;
        font-size: 40px;
        font-weight: 600;
        line-height: 1;
        color: #0d70b7; }
    &__definition {
        font-family: $montserrat;
        font-size: 24px;
        font-weight: 700;
        line-height: 1.25;
        color: #636161;
        margin-top: 60px;
        font-style: italic; }
    &__form {
        margin-bottom: 56px; } }

.calculator {
    padding: 30px 60px 40px;
    font-family: $arial;
    border-radius: 10px;
    background-color: $white;
    box-shadow: 0px 2px 21px 0 rgba(0,0,0,0.2);
    background-size: contain;
    background-position: 0 0;
    background-repeat: no-repeat;
    &__title {
        font-size: 30px;
        font-weight: 600;
        word-spacing: 0.02em;
        line-height: 1;
        color: #eb891a;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 24px;
        .icon {
            font-size: 96px;
            margin-right: 39px; } }
    &__group {
        margin-bottom: 94px;
        display: flex;
        align-items: center;
        justify-content: center;
        &_result {
            margin-top: 130px; }
        &_center {
            margin-bottom: 70px; } }
    &__label {
        width: 204px;
        padding: 7px 10px 5px;
        font-size: 20px;
        line-height: 1.5;
        text-align: center;
        color: #fdf9f9;
        background-color: #ff9d11; }
    &__range {
        width: 496px;
        margin: 0 64px;
        position: relative; }
    &__down {
        display: flex;
        flex-flow: column;
        position: absolute;
        transform: translateX(-50%);
        top: -52px;
        left: calc(50% + 7px);
        font-family: $montserrat;
        text-align: center;
        font-weight: 300;
        line-height: 1.3;
        font-size: 14px;
        font-style: italic;
        color: #979797;
        span {
            display: inline-block; }

        .icon {
            font-size: 9px;
            color: #979797;
            line-height: 1; } }
    &__input {
        font-family: $montserrat;
        font-size: 16px;
        height: 32px;
        width: 96px;
        border: 2px solid #cccccc;
        text-align: center; }
    &__result-descr {
        position: absolute;
        top: 40px;
        left: -10px;
        font-family: $montserrat;
        font-size: 16px;
        font-weight: 600;
        line-height: 1.5;
        color: #6b6b6b; } }




.blue-label {
    font-family: $arial;
    font-size: 20px;
    line-height: 1.67;
    color: #1b77b9;
    font-weight: 600; }
.mr-60 {
    margin-right: 60px; }

.noUi-horizontal .noUi-handle {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #0d70b7;
    box-shadow: none;
    &:before,
    &:after {
        opacity: 0; } }
.noUi-connects,
.noUi-base {
    background: #979797;
    height: 4px;
    border-radius: 2px; }
.noUi-target {
    border: none;
    background: none;
    box-shadow: none; }
.noUi-horizontal {
    height: 4px; }


.calculator__result {
    pointer-events: none;
    width: 670px;
    margin-left: 40px;
    &.noUi-horizontal {
        height: 18px; }
    .noUi-base {
        height: 18px;
        // background: linear-gradient(to right, #f3b000, #d51400)
        // border-radius: 9px
        &:before {
            position: absolute;
            content: '';
            top: 0;
            left: -10px;
            right: -10px;
            bottom: 0;
            background: linear-gradient(to right, #f3b000, #d51400);
            height: 18px;
            border-radius: 9px; } }
    .noUi-connects {
        height: 18px;
        background: linear-gradient(to right, #f3b000, #d51400);
        border-radius: 9px; }


    &.noUi-horizontal {
        .noUi-handle {
            height: 0;
            width: 0;
            right: 0 !important; } }
    .noUi-tooltip {
        border: none;
        color: red;
        padding: 0;
        padding-bottom: 36px;
        font-weight: 700;
        background: none;
        font-family: $arial;
        font-size: 18px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.67;
        letter-spacing: normal;
        text-align: left;
        color: #e9082f;
        &:after {
            content: '';
            position: absolute;
            top: -20px;
            left: 50%;
            transform: translateX(-50%);
            background: url(../img/range_tooltip_v2.svg) no-repeat;
            z-index: -1;
            width: 71px;
            height: 111px;
            background-size: cover; } } }

