.healthy {
	padding: 70px 0 54px;
	&__inner {
		margin: 0 auto;
		padding: 0 87px 0 15px;
		max-width: 1572px; } }

[data-tabs] {
	overflow: hidden;
	[data-tabs-control] {
		cursor: pointer;
		user-select: none;
		&[disabled],
		&.is-disabled {
			cursor: not-allowed;
			@include nosel; } }
	[data-tabs-wrapper] {
		position: relative; }
	[data-tabs-container] {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		&.is-open {
			position: relative;
			opacity: 1;
			visibility: visible; } } }
.search-md {
	position: relative;
	width: 448px;
	&__input {
		input {
			padding: 0 70px 0 20px;
			height: 47px;
			width: 100%;
			font-family: $montserrat;
			font-weight: 300;
			font-style: italic;
			font-size: 18px;
			border: 2.51px solid #0d70b7;
			border-radius: 10px;
			display: block;
			&::placeholder {
				color: #7f8183; }
			&:focus {
				&::placeholder {
					font-size: 0; } } } }
	&__button {
		position: absolute;
		top: 8px;
		right: 32px;
		line-height: 1;
		font-size: 31px;
		color: #0d70b7;
		background-color: transparent; } }
.tabs {
	&__out {
		position: relative;
		padding-left: 470px; }
	&__search {
		position: absolute;
		top: 10px;
		left: 0; }
	&__list {
		font-size: 0;
		text-align: right; }
	&__item {
		position: relative;
		padding: 26px 22px 24px 116px;
		color: #0D70B7;
		font-family: $montserrat;
		font-size: 30px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 1;
		vertical-align: top;
		transition: background-color .3s;
		background-color: #ccc;
		display: inline-block;
		&:before {
			position: absolute;
			content: "";
			top: 100%;
			left: 0;
			opacity: 0;
			transition: opacity .3s;
			@include arr(92,27,#e1e1e1,b); }
		&:not(:first-child) {
			margin-left: 76px; }
		&.is-active {
			&:before {
				opacity: 1; } } }
	&__icon {
		position: absolute;
		top: 0;
		left: 0;
		width: 92px;
		text-align: center;
		height: 80px;
		line-height: 78px;
		background-color: #e1e1e1;
		.icon {
			vertical-align: middle;
			line-height: 1;
			font-size: 53px;
			display: inline-block; }
		.icon-fruit {
			font-size: 59px; } }
	&__container {
		padding-top: 56px; }
	&__title-sm {
		margin-bottom: 45px; } }

.slider-recipe-wrap {
	padding: 0 40px; }
.slider-recipe {
	&__slide {
		padding: 0 20px; }
