	&__prev,
	&__next {
		position: absolute;
		top: 96px;
		cursor: pointer;
		background-color: transparent;
		.icon {
			font-size: 68px;
			color: #676a6c; } }
	&__prev {
		left: -40px; }
	&__next {
		right: -40px; }
	&__container {} }
